<template>
  <b-modal id="createTeacherModal"
           ref="modal"
           title="  "
           @ok="handleOk"
           size="lg"
           centered>
    <b-form @submit.stop.prevent="handleSubmit">
      <b-form-group id="teacherFirstNameLabel"
                    label=" :"
                    label-for="teacherFirstName">
        <b-form-input id="teacherFirstName"
                      type="text"
                      v-model="teacher.firstName"
                      required
                      placeholder="  ">
        </b-form-input>
      </b-form-group>
      <b-form-group id="teacherLastNameLabel"
                    label=" :"
                    label-for="teacherLastName">
        <b-form-input id="teacherLastName"
                      type="text"
                      v-model="teacher.lastName"
                      required
                      placeholder="  ">
        </b-form-input>
      </b-form-group>
      <b-form-group id="positionListLabel"
                    label=":"
                    label-for="positiontist">
        <b-form-select id="positionList"
                       v-model="teacher.position"
                       required>
          <option></option>
          <option></option>
          <option> </option>
          <option>  </option>
          <option> - </option>
          <option>  </option>
          <option>  </option>
          <option> </option>
          <option>  </option>
          <option>  </option>
          <option>  </option>
          <option>  </option>
          <option>  </option>
          <option>  </option>
          <option>  </option>
          <option>  </option>
          <option>  </option>
          <option>  </option>
          <option> - </option>
          <option>  </option>
          <option>  </option>
          <option> </option>
          <option>  </option>
          <option>  </option>
          <option>  </option>
          <option>  </option>
          <option>  </option>
          <option>  </option>
          <option>  </option>
          <option>  </option>
          <option>  </option>
        </b-form-select>
      </b-form-group>
      <b-form-group id="departmentListLabel"
                    label=":"
                    label-for="departmentList">
        <b-form-select id="departmentList"
                       v-model="teacher.departmentId"
                       required>
          <option
            v-for="department in departments"
            :key="department.id"
            :value="department.id">
            {{ department.name }}
          </option>
        </b-form-select>
      </b-form-group>
    </b-form>
    <div slot="modal-footer" class="w-100">
      <b-btn type="submit" size="sm" class="float-right" variant="primary" @click="handleOk">
        
      </b-btn>
      <b-btn type="reset" size="sm" class="float-right" variant="danger" @click="handleClose">
        
      </b-btn>
    </div>
  </b-modal>
</template>

<script>
import axios from 'axios'
export default {
  name: 'TeacherModalCreate',

  data () {
    return {
      teachers: null,
      departments: null,
      teacher: {
        firstName: '',
        lastName: '',
        position: '',
        departmentId: '',
        department: {
          name: ''
        }
      }
    }
  },

  methods: {
    getTeachers () {
      axios
        .get('/teachers')
        .then(response => {
          this.teachers = response.data
        })
        .catch(error => {
          console.log(error)
        })
    },
    getDepartments () {
      axios
        .get('/departments')
        .then(response => {
          this.departments = response.data
        })
        .catch(error => {
          console.log(error)
        })
    },
    handleClose () {
      this.$refs.modal.hide()
    },
    handleOk (evt) {
      evt.preventDefault()
      if (!this.teacher.firstName) {
        alert('   .')
      } else if (!this.teacher.lastName) {
        alert('   .')
      } else if (!this.teacher.position) {
        alert('    .')
      } else if (!this.teacher.department) {
        alert('    .')
      } else {
        this.handleSubmit()
      }
    },
    handleSubmit () {
      axios.post(
        `/teachers`,
        this.teacher
      )
        .catch(error => {
          console.log(error)
          alert('   .')
        })
        .then(success => {
          this.$emit('createTeacher')
          this.$refs.modal.hide()
        })
    }
  },

  mounted () {
    this.getTeachers()
    this.getDepartments()
  }
}
</script>

<style scoped>
  .float-right {
    margin-left: 10px;
  }
</style>



