<template>
  <div class="container-fluid">
    <section v-if="errored" class="errored">
      <p> ,       </p>
      <router-link to="/">
        <b-button> </b-button>
      </router-link>
    </section>
    <section v-else>
      <div class="jumbotron">
        <div class="page-header">
          <h1>{{ subject.name }}</h1>
          <p class="faculty-text">{{ subject.description }}</p>
        </div>
      </div>
      <div>
        <b-form @submit="onSubmit" v-if="show" class="form-class">
          <b-form-group id="subjectNameLabel"
                        label=" :"
                        label-for="subjectName">
            <b-form-input id="subjectName"
                          type="text"
                          v-model="subject.name"
                          required
                          placeholder="  ">
            </b-form-input>
          </b-form-group>
          <!--   -->
          <b-form-group id="subjectDescriptionLabel"
                        label=" :"
                        label-for="subjectDescription">
            <b-form-textarea id="subjectDescription"
                             type="text"
                             v-model="subject.description"
                             required
                             :rows="3"
                             :max-rows="6"
                             placeholder="  ">
            </b-form-textarea>
          </b-form-group>
          <!--  -->
          <b-form-group id="teacherListLabel"
                        label=":"
                        label-for="teacherList">
            <b-form-select id="teacherList"
                           v-model="subject.teacherId"
                           required
                           multiple>
              <option
                v-for="teacher in teachers"
                :key="teacher.id"
                :value="teacher.id">
                {{ teacher.firstName + ' ' + teacher.lastName}}
              </option>
            </b-form-select>
          </b-form-group>

          <!-- :    -->
          <b-button type="submit" variant="primary"></b-button>
          <b-button variant="danger" @click="deleteSubject"></b-button>
        </b-form>
      </div>
      <hr/>
      <!--     -->
      <b-button @click="goBack"></b-button>
    </section>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'StudentEdit',

  data () {
    return {
      teachers: null,
      subject: {
        id: this.$route.params.id,
        name: '',
        description: '',
        teacherId: []
      },
      errored: false,
      show: true
    }
  },

  methods: {
    getSubject () {
      if (this.subject.id !== undefined) {
        axios
          .get(`/subjects/${this.subject.id}`)
          .then(response => {
            this.subject = response.data
          })
          .catch(error => {
            console.log(error)
            this.errored = true
          })
      }
    },
    getTeachers () {
      axios
        .get('/teachers')
        .then(response => {
          this.teachers = response.data
        })
        .catch(error => {
          console.log(error)
        })
    },
    deleteSubject () {
      axios
        .delete(`/subjects/${this.subject.id}`)
        .then(response => {
          console.log(response.data)
          this.$router.go(-1)
          alert(' ')
        })
        .catch(error => {
          console.log(error)
          alert('   ' + error)
        })
    },
    onSubmit (evt) {
      evt.preventDefault()
      axios.put(
        `/subjects/${this.subject.id}`,
        this.subject
      )
        .then(resp => alert(' '))
        .catch(err => console.error(err))
    },
    onReset (evt) {
      evt.preventDefault()
      /* Reset our form values */
      this.subject.name = ''
      this.subject.description = ''
      this.this.subject.teacherId = ''
      /* Trick to reset/clear native browser form validation state */
      this.show = false
      this.$nextTick(() => { this.show = true })
    },
    goBack () {
      this.$router.go(-1)
    }
  },

  mounted () {
    this.getSubject()
    this.getTeachers()
  }
}
</script>

<style scoped>
  .jumbotron {
    background-color: #f7f7f7;
    padding: 20px;
    padding-bottom: 10px;
    margin-top: 25px;
  }
  .form-class {
    max-width: 1000px;
    text-align: left;
    margin: auto;
  }
  .faculty-text {
    margin: auto;
    font-size: 13px;
    max-width: 40%;
  }
  .container-fluid {
    padding: 0px;
  }
</style>



