<template>
  <div class="container-fluid">
    <section v-if="errored" class="errored">
      <p> ,       </p>
      <router-link to="/">
        <b-button> </b-button>
      </router-link>
    </section>
    <section v-else>
      <div class="jumbotron">
        <div class="page-header">
          <h1>{{ specialty.name }}</h1>
          <p>{{ specialty.description }}</p>
          <p class="faculty-text">{{ specialty.department.name }}</p>
        </div>
      </div>
      <div>
      <b-form @submit="onSubmit" v-if="show" class="form-class">
        <b-form-group id="specialtyNameLabel"
                      label=" :"
                      label-for="specialtyName">
          <b-form-input id="specialtytName"
                        type="text"
                        v-model="specialty.name"
                        required
                        placeholder="  ">
          </b-form-input>
        </b-form-group>
        <b-form-group id="specialtyDescriptionLabel"
                      label=" :"
                      label-for="specialtyDescription">
          <b-form-textarea id="specialtyDescription"
                           v-model="specialty.description"
                           placeholder="  "
                           required
                           :rows="3"
                           :max-rows="6">
          </b-form-textarea>
        </b-form-group>
        <b-form-group id="specialtyListLabel"
                      label=":"
                      label-for="specialtyList">
          <b-form-select id="specialtyList"
                         v-model="specialty.departmentId"
                         required>
            <option
              v-for="department in departments"
              :key="department.id"
              :value="department.id">
              {{ department.name }}
            </option>
          </b-form-select>
        </b-form-group>
        <b-button type="submit" variant="primary"></b-button>
        <b-button variant="danger" @click="deleteSpecialty"></b-button>
      </b-form>
      </div>
      <hr/>
      <router-link :to="'/departments/' + specialty.departmentId">
        <b-button></b-button>
      </router-link>
    </section>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'SpecialtyEdit',

  data () {
    return {
      specialty: {
        id: this.$route.params.id,
        name: '',
        description: '',
        departmentId: '',
        department: {
          name: '',
          description: ''
        }
      },
      studentGroups: null,
      departments: null,
      errored: false,
      show: true
    }
  },

  methods: {
    getSpecialty () {
      if (this.specialty.id !== undefined) {
        axios
          .get(`/specialties/${this.specialty.id}`)
          .then(response => {
            this.specialty = response.data
          })
          .catch(error => {
            console.log(error)
            this.errored = true
          })
      }
    },
    getDepartments () {
      axios
        .get('/departments')
        .then(response => {
          this.departments = response.data
        })
        .catch(error => {
          console.log(error)
        })
    },
    deleteSpecialty () {
      const studentGroups = this.getSpecialtyStudentGroups()
      if (studentGroups != null) {
        alert('  ,      .' +
          '        .')
      } else {
        axios
          .delete(`/specialties/${this.specialty.id}`)
          .then(response => {
            console.log(response.data)
            this.$router.push(`/departments/${this.specialty.departmentId}`)
            alert(' ')
          })
          .catch(error => {
            console.log(error)
            alert('   ' + error)
          })
      }
    },
    getSpecialtyStudentGroups () {
      if (this.specialty.id !== undefined) {
        axios
          .get(`/specialties/${this.specialty.id}/student-groups`)
          .then(response => {
            this.studentGroups = response.data
          })
          .catch(error => {
            console.log(error)
          })
      }
    },
    onSubmit (evt) {
      evt.preventDefault()
      axios.put(
        `/specialties/${this.specialty.id}`,
        this.specialty
      )
        .then(resp => alert(' '))
        .catch(err => console.error(err))
    },
    onReset (evt) {
      evt.preventDefault()
      /* Reset our form values */
      this.specialty.name = ''
      this.specialty.description = ''
      this.specialty.department.name = ''
      /* Trick to reset/clear native browser form validation state */
      this.show = false
      this.$nextTick(() => { this.show = true })
    }
  },

  mounted () {
    this.getSpecialty()
    this.getDepartments()
    this.getSpecialtyStudentGroups()
  }
}
</script>

<style scoped>
  .jumbotron {
    background-color: #f7f7f7;
    padding: 20px;
    padding-bottom: 10px;
    margin-top: 25px;
  }
  .form-class {
    max-width: 1000px;
    text-align: left;
    margin: auto;
  }
  .faculty-text {
    margin: auto;
    font-size: 12px;
    background-color: lightgray;
    max-width: 50%;
  }
  .container-fluid {
    padding: 0px;
  }
</style>



